import { Component, OnInit } from '@angular/core';
import { DataService } from 'src/app/services/data.service';
import * as moment from 'moment';
import { SubjectSubscriber } from 'rxjs/internal/Subject';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css']
})
export class TableComponent implements OnInit {
  subscriptions: Subscription = new Subscription();
  tableData = [];
  startDate = moment('10/20/20');
  today = moment();
  totalDays = this.today.diff(this.startDate, 'days')
  tableMonths = [];
  goalData: Subscription;
  goalSlugs;

  constructor(private ds: DataService) { }

  ngOnInit() {
    // set goalSlugs
    // this.goalSlugs = this.ds.goalSlugs;

    // init tableData
    for (let i = 0; i < this.totalDays; i++) {
      let t = moment();
      let d = {
        date: t.subtract(i, 'd').toDate(),
        legExercises: {completed: false},
        turmeric: {completed: false},
        pushups: {amount: null}
      }
      this.tableData.push(d);
    }

    // save init data to database
    // this.tableData.forEach((d) => {
    //   this.ds.createDate(d).then(res => {
    //     if (res.id) {
    //       d.id = res.id;
    //       this.ds.updateDate(d.id, d);
    //     }
    //   })
    // })

    // set months
    this.tableData.forEach((d) => {
      const month = moment(d.date).format('MMMM')
      let monthsInd;

      const alreadyExists = this.tableMonths.some((m, i) => {
        if (m.month && m.month === month) {
          monthsInd = i;
          return true;
        }
        return false;
      })

      if (alreadyExists) {
        this.tableMonths[monthsInd].amount++;
      } else {
        this.tableMonths.push({month: month, amount: 1})
      }
    })

    // replace blank table data with actual
    this.subscriptions.add(
      this.ds.getDates().subscribe(
        (datesArr) => {
          datesArr.forEach((db) =>{
            this.tableData.forEach((tb, tInd) => {
                const dbDate = moment(db.date.toDate()).format('MM-DD-YYYY');
                const tbDate = tb.id ? moment(tb.date.toDate()).format('MM-DD-YYYY') : moment(tb.date).format('MM-DD-YYYY');
                if (dbDate === tbDate) {
                  this.tableData[tInd] = {...db}
                }
            })
          })
        },
        (e) => console.error(e)
      )
    )

  }

  checkboxUpdate(data, index, slug) {
    data[slug].completed = !data[slug].completed;
    if (data.id) {
      this.ds.updateDate(data.id, data)
    } else {
      this.ds.createDate(data).then(res => {
        if (res.id) {
          data.id = res.id;
          this.tableData[index] = {...data}
          this.ds.updateDate(data.id, data)
        }
      })
    }
  }

}



