import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection } from '@angular/fire/firestore';
import { BehaviorSubject, from } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class DataService {
  constructor(private firestore: AngularFirestore) {}

  getDates() {
    const datesRef: AngularFirestoreCollection<any> = this.firestore.collection('dates');
    return from(datesRef.valueChanges());
  }
  updateDate(id, data) {
    this.firestore.doc('dates/' + id).update(data);
  }
  createDate(data) {
    return this.firestore.collection('dates').add(data);
  }


}
